.organizer {

   .container {
      @extend %container;
   }

   &__inner {
      @media screen and (min-width: 1280px) {
         display: flex;
         flex-direction: row-reverse;
         justify-content: start;
      }
   }

   &__card {
      display: flex;
      align-items: center;
      flex-direction: column;
   }

   &__name {
      margin-bottom: 7px;
      max-width: 200px;
      text-align: center;
      @include text(18px, 500, 22px, $textColor);
   }

   &__photographer {
      margin-bottom: 7px;
      @include text(14px, 400, 21px, $textColor);
   }

   &__link {
      max-width: 200px;
      text-align: center;
      @include text(14px, 500, 21px, $linkColor);
      margin-bottom: 25px;
   }

   &__photo-link {
      margin-bottom: 23px;
      position: relative;

      &::before {
         content: "";
         height: 1px;
         width: 668px;
         background: $secondaryColor;
         display: none;
         position: absolute;
         top: -56px;
         left: -265px;

         @media screen and (min-width: 768px) {
            display: block;
         }
         @media screen and (min-width: 1280px) {
            width: 1140px;
            left: -840px;
         }
      }

      &::after {
         content: "";
         background-image: url(./../images/heart.svg);
         display: block;
         background-repeat: no-repeat;
         height: 27px;
         width: 31px;
         position: absolute;
         left: 186px;
         top: -27px;
      }
   }

   &__about {
      @media screen and (min-width: 1280px) {
         margin-right: 226px;
      }
   }

   &__subtitle {
      @include text(18px, 500, 26px, $textColor);
      margin-bottom: 14px;
   }

   &__text {
      max-width: 584px;
      @include text(16px, 400, 24px, $textColor);
   }

   &__offer {
      max-width: 584px;
      @include text(16px, 400, 24px, $textColor);
   }

   &__link-text {
      margin: 22px 0 30px;
      @include text(16px, 500, 23px, $linkColor);
      position: relative;

      &::after {
         content: "";
         background-image: url(./../images/Arrow.svg);
         display: block;
         background-repeat: no-repeat;
         height: 6px;
         width: 11px;
         position: absolute;
         top: 9px;
         right: -20px;
      }
      @media screen and (min-width: 576px) {
         margin-bottom: 73px;
      }
   }
}