.gallery {
   background-color: $primaryColor;

   .container {
      @extend %container;
   }

   &__inner {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-top: 74px;
   }

   &__title {
      width: 280px;
      text-align: center;
      margin: 0 auto;
      margin-bottom: 32px;
      @include text(28px, 400, 34px, $textColor);

      @media screen and (min-width: 768px) {
         text-align: left;
         margin: 0 0 30px 0; 
         @include text(20px, 500, 29px, $textColor);
      }
   }

   &__example {
      display: flex;
      overflow: auto;
      margin-bottom: 64px;
   }

   &__reference {
      margin-right: 15px;
      max-height: 185px;
      max-width: 276px;
      width: 100%;
      object-fit: cover;
   }

   &__link {
      margin: 0 auto;
      margin-bottom: 83px;
      @include text(17px, 500, 25px, $linkColor);
      position: relative;
      display: none;

      &::before {
         content: "";
         background-image: url(./../images/Arrow.svg);
         display: block;
         background-repeat: no-repeat;
         height: 6px;
         width: 11px;
         position: absolute;
         top: 9px;
         left: -20px;
         
      }

      &::after {
         content: "";
         background-image: url(./../images/Arrow.svg);
         display: block;
         background-repeat: no-repeat;
         height: 6px;
         width: 11px;
         position: absolute;
         top: 9px;
         right: -20px;
      }

      @media screen and (min-width: 768px) {
         display: block;
      }
   }
}